body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
  


}
ul{
  margin: 0px;
  padding: 0px;
  list-style: none;
}
a{
  text-decoration: none;
  color:black;
}
a:hover {
  color: black;
}
 /* NAVBAR */
.header{
  position: sticky;
  top: 0px;
  left: 0px;
  z-index: 100;
  opacity: 0.9;
  box-shadow:0px 3px rgba(0, 0,0, 0.2);
}

.navbar-brand{
  padding: 0 0 0 1rem;

}
.navbar-brand img{
  width: 50px;
  height: 50px;
}
.login{
  display: flex;
  float: right;
  
}

.login img{

  width: 32px;
  height: 32px;
  border-radius: 32px;
}
.nav-item{
  font-size: 12px;
  font-weight: 500;
  padding: 0px 5px;

 
}
.navbar-light .navbar-list .nav-item .nav-link{
  color: black;
}
.navbar-light .navbar-list .nav-item .nav-link:hover{
  color: rgb(238, 14, 14);
}
.navbar-light .navbar-log .nav-item .nav-link:hover{
  color: rgb(14, 104, 238);
}
@media (min-width:768px)
  {
    .navbar-list{
      padding-left: 13%;
    }
    ul.navbar-log{
      height: 34px;
    }
  }
@media (min-width:992px)
  {
    .navbar-list{
      padding-left: 27%;
    }
  }
  @media (max-width:767px)
  {
    .navbar-light .nav-item{
    padding: 7px 15px;
    }
    .navbar-light button{
      border: none;
    }
  }

/* SLIDER  */
  
.slick-slider button.slick-arrow{

 
 font-size: 30px;
 width: 40px;
 
}
.slick-slider button.slick-arrow::before{
  font-size: 50px;
  color: red;
 background-color: none;
 
}
.slick-slider button.slick-prev{
  
 margin-left: 80px;
 
 z-index: 10;
}
.slick-slider button.slick-next{
  margin-right: 80px;
  z-index: 10;
 }

.slick-slider ul.slick-dots{
  bottom: 50px;
 
}
ul.slick-dots li button::before{
  font-size: 14px;
  color: antiquewhite;
}
ul.slick-dots li.slick-active button::before{
  color: yellow;
}
.anhchay{
  width: 100%;
}
@media (max-width:992px) {

  .slick-slider button.slick-prev{
    z-index: -1;
  }
  .slick-slider button.slick-next{
    z-index: -1;
  }
}

@media (max-width:768px) {

  .slick-slider ul.slick-dots{
    bottom: 30px;
   
  }
}

@media (max-width:576px) {

  .slick-slider ul.slick-dots{
    bottom: 15px;
   
  }
  ul.slick-dots li button::before{
    font-size: 12px;
   
  }
}
@media (max-width:400px) {

  .slick-slider ul.slick-dots{
    bottom: 12px;
   
  }
  ul.slick-dots li button::before{
    font-size: 8px;
    
   
  }
 
}


/* LIST PHIM  */
.tung-phim{
  width: 100%;
