  margin-right: -20px;
}
.khung-phim .slick-slider button.slick-arrow::before
{
  color: aqua;
}

.danhgia{
  position: absolute;
  top: 10px;
  right: 15px;
}
.khung-mo{
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  overflow: hidden;
  height: 270px;
  
  opacity: 0;
  z-index: -1;
  transition: all 1s;
  border-radius: 5px;
}
