  width: 50px;
  height: 50px;
  margin: auto;
  margin-top: 110px;
}
.khung-phim .khung-video img{
  width: 50px;
  height: 50px;
}
button.datve{
  width: 87%;
  margin-left: 6.5%;
  position: absolute;
  bottom: -40px;
  transition: all 1s;
}
.tung-phim:hover .khung-mo{
  z-index: 1;
  opacity: 1;
  background-color: rgba(0, 0,0, 0.4);
}
.tung-phim:hover .khung-mo button{
  bottom: 0;
}
.frame-video{
  
  height: 420px;
 
}
.ten-phim{
  -webkit-line-clamp: 1;
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
}
@media (max-width:992px)
{
  .khung-phim .slick-slider button.slick-prev{
    display: none;
    opacity: 0;
  }
  .khung-phim .slick-slider button.slick-next{
    display: none;
    opacity: 0;
  }
  .frame-video{
  
    height: 350px;
    
  }
}

